import React from 'react';
import './styles/Home.css'; 
import headshotImage from './images/profile.png'; 
import { FaGithub, FaLinkedin, FaEnvelope } from 'react-icons/fa'; 

const Home = () => {
  return (
    <div className="home">
      <header className="header">
        <h1>Welcome to My Portfolio</h1>
      </header>

      <section className="intro">
        <div className="intro-item">
          <img src={headshotImage} alt="Headshot" className="headshot" />
        </div>
        <div className="intro-item">
          <p>
          Hi, I'm Abdouaziz Jom, a junior studying Computer and Information Science at the University of Michigan. I've always been passionate about technology and the power it holds to innovate. I particularly enjoy software engineering, game development, and frontend development. I continually strive to learn and explore new technical fields. I find joy in collaborating with others and am enthusiastic about working with people to create meaningful and impactful projects.
          </p>
          <div className="social-links">
            <a href="https://github.com/azjom"><FaGithub /></a>
            <a href="mailto:abdou.jom16@gmail.com"><FaEnvelope /></a>
            <a href="https://linkedin.com/in/abdouaziz-jom-9b6a68220"><FaLinkedin /></a>
          </div>
        </div>
      </section>

      <section className="skills">
        <h2>Skills</h2>
        <div className="skills-list">
          <ul>
            <li>Languages: C++, C#, C, Python, Julia, R, Matlab, JavaScript</li>
            <li>Frameworks: Git/Github, VSCode, SolidWorks, Google Sheets, Microsoft Excel, HTML, CSS, React, Flask, AWS, SQLite3, RestAPI</li>
          </ul>
        </div>
      </section>
    </div>
  );
};

export default Home;
